import utils from '~/common/utils'
import TagInput from '~/components/TagInput'
import MarkdownHelp from '~/components/MarkdownHelp'
import MarkdownEditor from '~/components/MarkdownEditor'

export default {
  middleware: 'authenticated',
  components: {
    TagInput,
    MarkdownHelp,
    MarkdownEditor
  },
  data() {
    return {
      publishing: false, // ÂΩìÂâçÊòØÂê¶Ê≠£Â§Ñ‰∫éÂèëÂ∏É‰∏≠...
      postForm: {
        title: '',
        tags: [],
        content: ''
      }
    }
  },
  computed: {
    user() {
      return this.$store.state.user.current
    }
  },
  mounted() {},
  methods: {
    async submitCreate() {
      const me = this
      if (me.publishing) {
        return
      }
      me.publishing = true
      try {
        const article = await this.$axios.post('/api/article/create', {
          title: me.postForm.title,
          content: me.postForm.content,
          tags: me.postForm.tags ? me.postForm.tags.join(',') : ''
        })
        this.$refs.mdEditor.clearCache()
        this.$toast.success('Êèê‰∫§ÊàêÂäü', {
          duration: 1000,
          onComplete() {
            utils.linkTo('/article/' + article.articleId)
          }
        })
      } catch (e) {
        me.publishing = false
        this.$toast.error('Êèê‰∫§Â§±Ë¥•Ôºö' + (e.message || e))
      }
    }
  },
  head() {
    return {
      title: this.$siteTitle('ÂèëË°®ÊñáÁ´†')
    }
  }
}
